import React from 'react';
import Layout from './Layout';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Paper,
  Typography,
  Chip,
  Button,
  Grid,
  Link,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import TableSample from '../component/TableSample.js';

const Notice = () => {
  return (
    <Layout>
      <Paper
        elevation={0}
        direction='column'
        justifyContent='center'
        alignItems='center'
        style={{
          width: '80%',
          margin: 'auto',
          paddingBottom: '5%',
          paddingTop: '2%',
        }}
      >
        <Typography variant='h4' margin={2}>
          
        </Typography>
        <Accordion>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Chip label='FAQ' size='small' style={{ marginRight: '5px' }} />
            <Typography>   ?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
                14       ,{' '}
              <br />
                   3~5 . <br />
              5        1:1  
               .
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Chip label='FAQ' size='small' style={{ marginRight: '5px' }} />
            <Typography>   </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
                      , <br />
              1:1    .
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Chip label='' size='small' style={{ marginRight: '5px' }} />
            <Typography> </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
                   ,   
              . <br />
                      
              .
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Grid container justifyContent='flex-end'>
          <Link href='/notice/new' underline='none'>
            <Button
              variant='contained'
              style={{ marginTop: '1rem', marginBottom: '1rem', backgroundColor: '#7289da' }}
            >
              1:1 
            </Button>
          </Link>
        </Grid>
        <TableSample />
      </Paper>
    </Layout>
  );
};

export default Notice;
