import React from 'react';
import { Link } from 'react-router-dom';
import {
  Paper,
  Typography,
  Grid,
  Chip,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import gif from '../assets/indexGIF.gif';
